import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	document.ref = React.createRef();

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
			if(type === 'VKWebAppShowStoryBoxFailed'){
				alert(JSON.stringify(data));
			}
		});
		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		//setActivePanel(e.currentTarget.dataset.to);
		alert(JSON.stringify(document.ref.current));
		connect.send("VKWebAppShowStoryBox",{"background_type":"image","url":"https://static.vovadev.ru/story.png","locked":true,"stickers":[
			{
				"sticker_type":"renderable",
				"sticker":{
					"content_type":"image",
					"url": "https://static.vovadev.ru/stick.png",
					"transform": {
						"rotation":0,
						"relation_width":0.3,
						"gravity":"center"
					},
					"clickable_zones":[
						{
							"action_type":"link",
							"action":{
								"link":"https://vk.com/everydayyaroslav",
								"tooltip_text_key":"Да-да, сюда"
							},
							"transform":[
								{
									"x":0,
									"y":0
								},
								{
									"x":512,
									"y":0
								},
								{
									"x":512,
									"y":512
								},
								{
									"x":0,
									"y":512
								}
							]
						}
					],
					"original_width":512,
					"original_height":512,
					"can_delete":false
				}
			}
		]});
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} ref={document.ref} />
			<Persik id='persik' go={go} />
		</View>
	);
}

export default App;

